import React, { memo, useRef } from 'react'
import { Carousel } from 'antd';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';

import { BannerWrapper } from './style';

const HomeBanner = memo(() => {
  const carousel = useRef()

  function prevHandle() {
    carousel.current?.prev()
  }
  function nextHandle() {
    carousel.current?.next()
  }

  return (
    <BannerWrapper>
      <div className="banner_btn prev" onClick={prevHandle}><LeftOutlined /></div>
      <div className="banner_btn next" onClick={nextHandle}><RightOutlined /></div>
      <Carousel autoplay ref={carousel}>
        <div>
            <img src={require("@/assets/image/banner1.webp")} alt="" />
        </div>
        <div>
            <img src={require("@/assets/image/banner2.webp")} alt="" />
        </div>
        <div>
            <img src={require("@/assets/image/banner3.webp")} alt="" />
        </div>
      </Carousel>
    </BannerWrapper>
  )
})

export default HomeBanner